@import url('https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i|Oswald:200,300,400,500,600,700&display=swap&subset=cyrillic,cyrillic-ext');
aside, nav, footer, header, section, article { display: block }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
textarea{
  resize:none;
}
input, textarea { outline:none; }
input:active, textarea:active { outline:none; }
:focus { outline:none; }
@mixin after() {
  content:'';
  display:block;
  clear:both;
}
@mixin transition() {
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
@mixin transition2() {
  -webkit-transition: 0.7s;
  -moz-transition: 0.7s;
  -ms-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
}
body {
  margin:0;
  color:#000;
  font-family: 'Montserrat', sans-serif;
  font-size:14px;
  background: #2F3951;
  min-width:320px;
  height:100%;
  position:relative;
  -webkit-text-size-adjust: none;
}
*{
  box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  -o-box-sizing:border-box;
  -ms-box-sizing:border-box;
}
.line-top{
  position: absolute;
  top: 84px;
  left: 0;
  width: 100%;
  height: 16px;
  padding: 0 44px;
  z-index: 5;
  .line{
    border-top: 1px solid rgba(87, 157, 197, 0.4);
  }
}
.line-right{
  position: absolute;
  top: 0;
  right: 44px;
  width: 16px;
  height: 100%;
  padding: 84px 0 36px;
  z-index: 5;
  .line{
    border-right: 1px solid rgba(87, 157, 197, 0.4);
  }
}
.line-bottom{
  position: absolute;
  bottom: 36px;
  left: 0;
  width: 100%;
  height: 16px;
  padding: 0 44px;
  z-index: 5;
  .line{
    border-bottom: 1px solid rgba(87, 157, 197, 0.4);
  }
}
.line-left{
  position: absolute;
  top: 0;
  left: 44px;
  width: 16px;
  height: 100%;
  padding: 84px 0 36px;
  z-index: 5;
  .line{
    border-left: 1px solid rgba(87, 157, 197, 0.4);
  }
}
.line{
  display: block;
  width: 100%;
  height: 100%;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  overflow: hidden;
}
#wrapper{
  overflow:hidden;
  position: relative;
}
.header{
  min-height: 84px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  z-index: 2;
}
.btn-nav{
  cursor: pointer;
  position: absolute;
  top: 36px;
  right: 29px;
  display: flex;
  flex-direction: column;
  @include transition;
  align-items: flex-end;
  b{
    display: block;
    height: 1px;
    @include transition;
    width: 27px;
    background: #8A8ED1;
    margin-bottom: 5.5px;
    &:nth-child(2) {
      width: 20px;
    }
    &:nth-child(3) {
      margin: 0;
      width: 13px;
    }
  }
  &:hover b{
    width: 27px;
  }
}
.nav{
  ul{
    display: flex;
  }
  li{
    margin: 0 22px;
  }
  a{
    display: block;
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    text-transform: uppercase;
    color: #8A8ED1;
    @include transition;
    &:hover{
      color: #fff;
    }
  }
}
.main-screen{
  height: 755px;
  overflow: hidden;
  position: relative;
  z-index: 1;
  img.main-img{
