    min-width: 100%;
    height: auto;
    min-height: 755px;
    position: absolute;
    top:0;
    left: 0;
  }
  img.dop-img{
    display: block;
    position: absolute;
    bottom:0;
    left: 50%;
    -webkit-transform: translate(-50%,0);
    -moz-transform: translate(-50%,0);
    -ms-transform: translate(-50%,0);
    -o-transform: translate(-50%,0);
    transform: translate(-50%,0);
    z-index: 3;
  }
  img.dop-img2{
    display: block;
    position: absolute;
    bottom:0;
    left: 50%;
    -webkit-transform: translate(-50%,0);
    -moz-transform: translate(-50%,0);
    -ms-transform: translate(-50%,0);
    -o-transform: translate(-50%,0);
    transform: translate(-50%,0);
    z-index: 5;
  }
}
