.popup-menu{
  padding: 90px 15px;
  position: fixed;
  background: #0D0D18;
  width: 100%;
  height: 100%;
  z-index: 12;
  left: 0;
  top: -100%;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
  pointer-events: none;
  -webkit-transition: 2s;
  -moz-transition: 2s;
  -ms-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  -webkit-transition-delay: 1s;
  -moz-transition-delay: 1s;
  -ms-transition-delay: 1s;
  -o-transition-delay: 1s;
  transition-delay: 1s;
  &:after{
    @include after;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 80px;
    background: #0D0D18;
  }
  .fixed-block{
    opacity: 1;
    background: transparent;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
    top: 5px;
    pointer-events: none;
    z-index: 1;
  }
  .small-logo{
    top:0;
    left: 0;
  }
  .btn-nav{
    b{
      position: relative;
      -webkit-transform: rotate(45deg);
      -moz-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      -o-transform: rotate(45deg);
      transform: rotate(45deg);
      width: 27px;
      background: #8A8ED1;
      top: 4px;
      &:nth-child(2) {
        display: none;
      }
      &:last-child{
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        transform: rotate(-45deg);
        width: 27px;
        top: -3px;
      }
    }
  }
}
